import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
//import { HistoricalTabPage } from '../../pages/historical-tab/historical-tab.page'
import { EventEmitter, Output } from '@angular/core';
import * as moment from 'moment';
@Component({
  selector: 'app-historical-new-date',
  templateUrl: './historical-new-date.component.html',
  styleUrls: ['./historical-new-date.component.scss'],
})
export class HistoricalNewDateComponent implements OnInit {
  newDateHistorical:Date;
  current_date:string;
  @Output() tabGotClosed = new EventEmitter();
  constructor(private modalCtrl: ModalController) { }

  ngOnInit() {}
  ionViewWillEnter(){
    this.current_date = moment(Date.now()).format('YYYY-MM-DD');
  }

  multipleFunctionExec(newDateHistorical){
    this.tabGotClosed.emit(newDateHistorical);
    this.newDateHistorical = newDateHistorical;
    //this.histTab.dateToday = newDateHistorical;
    this.close();
  }


  close() {
    this.modalCtrl.dismiss(this.newDateHistorical);
  }
  

}
