import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';


const routes: Routes = [
  { path: '', redirectTo: 'login', pathMatch: 'full' },
  { path: 'login', loadChildren: './pages/login/login.module#LoginPageModule' },
  { path: 'register', loadChildren: './pages/register/register.module#RegisterPageModule' },
  { path: 'additional-data', loadChildren: './pages/additional-data/additional-data.module#AdditionalDataPageModule' },
  { path: 'waist-measure', loadChildren: './pages/waist-measure/waist-measure.module#WaistMeasurePageModule' },
  { path: 'home', loadChildren: './pages/home/home.module#HomePageModule' },
  { path: 'profile', loadChildren: './pages/profile/profile.module#ProfilePageModule' },
  { path: 'historical', loadChildren: './pages/historical/historical.module#HistoricalPageModule' },
  { path: 'photo-register', loadChildren: './pages/photo-register/photo-register.module#PhotoRegisterPageModule' },
  { path: 'support-facilities', loadChildren: './pages/support-facilities/support-facilities.module#SupportFacilitiesPageModule' },
  { path: 'tips', loadChildren: './pages/tips/tips.module#TipsPageModule' },
  { path: 'historical-weight', loadChildren: './pages/historical-weight/historical-weight.module#HistoricalWeightPageModule' },
  { path: 'daily-meal', loadChildren: './pages/daily-meal/daily-meal.module#DailyMealPageModule' },
  { path: 'helper', loadChildren: './pages/helper/helper.module#HelperPageModule' },
  { path: 'camera-view', loadChildren: './pages/camera-view/camera-view.module#CameraViewPageModule' },
  { path: 'support-feedback', loadChildren: './support-feedback/support-feedback.module#SupportFeedbackPageModule' },
  { path: 'support-list', loadChildren: './support-list/support-list.module#SupportListPageModule' },
  { path: 'calendar', loadChildren: './pages/calendar/calendar.module#CalendarPageModule' },
  { path: 'user-meal-history', loadChildren: './pages/user-meal-history/user-meal-history.module#UserMealHistoryPageModule' },
  { path: 'historical-support', loadChildren: './pages/historical-support/historical-support.module#HistoricalSupportPageModule' },
  { path: 'support-meal-history', loadChildren: './pages/support-meal-history/support-meal-history.module#SupportMealHistoryPageModule' }






];


@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }