import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { about } from '../../../assets/hard-texts/about'
@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.scss'],
})
export class AboutComponent implements OnInit {
  about:string=about.text;
  teachers:string[]=about.teachers;
  students:string[]=about.students;
  teachers_backUp:string[]=about.teachers_backUp;
  students_backUp:string[]=about.students_backUp;
  
  constructor(private modalCtrl: ModalController) { 
    
  }
  ionViewWillEnter(){
    //var index_t = this.getRandomIntInclusive(0,about.teachers_backUp.length)
    //incluindo todos na lista
    var index_s = this.getRandomIntInclusive(0,about.students_backUp.length)
    this.teachers = this.teachers.concat(this.teachers_backUp/* [index_t] */).sort()
    this.students = this.students.concat(this.students_backUp).sort()
  }
  getRandomIntInclusive(min, max) {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }
  ngOnInit() {
    //console.log(this.teachers);
    
  }
  close() {
    this.modalCtrl.dismiss();
  }
}
