import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';
import { IonicModule } from '@ionic/angular';

import { HistoricalPage } from './historical.page';
import { HistoricalTabPage } from '../historical-tab/historical-tab.page';
import { HistoricalNewDateComponent } from 'src/app/modals/historical-new-date/historical-new-date.component';

const routes: Routes = [
  {
    path: 'historical',
    component: HistoricalPage,
    children: [
      {
        path: 'historical-tab',
        component: HistoricalTabPage,
        children: [
          {
            path: 'historical-tab1',
            loadChildren: '../historical-tab1/historical-tab1.module#HistoricalTab1PageModule'
          },
          {
            path: 'historical-tab2',
            loadChildren: '../historical-tab2/historical-tab2.module#HistoricalTab2PageModule'
          },
          {
            path: 'historical-tab3',
            loadChildren: '../historical-tab3/historical-tab3.module#HistoricalTab3PageModule'
          },
          {
            path: 'historical-tab4',
            loadChildren: '../historical-tab4/historical-tab4.module#HistoricalTab4PageModule'
          },
          {
            path: 'historical-tab5',
            loadChildren: '../historical-tab5/historical-tab5.module#HistoricalTab5PageModule'
          }
        ]
      },
    ]
  },
  {
    path: '',
    redirectTo: '/historical/historical-tab/historical-tab1',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    IonicModule,
    RouterModule.forChild(routes)
  ],
  declarations: [HistoricalPage, HistoricalTabPage, HistoricalNewDateComponent],
  entryComponents:[HistoricalNewDateComponent]
})
export class HistoricalPageModule {}
