import { Component, OnInit, Injectable } from '@angular/core';
import * as moment from 'moment';
import { ModalController } from '@ionic/angular';
import { HistoricalNewDateComponent } from 'src/app/modals/historical-new-date/historical-new-date.component';

@Component({
  selector: 'app-historical-tab',
  templateUrl: './historical-tab.page.html',
  styleUrls: ['./historical-tab.page.scss'],
})
export class HistoricalTabPage implements OnInit {
  dateToday:any;; 
  dateBeforeOne:any ;
  dateBeforeTwo:any; 
  dateBeforeThree:any;
  dateBeforeFour:any ;
 
     
  
  
  constructor(private modalCtrl: ModalController ) { }
    initDate = Date.now()
  
  ionViewWillEnter(){
    this.dateToday = moment(this.initDate).format('DD/MM');
    this.dateBeforeOne = moment(this.initDate).subtract(1, 'days').format('DD/MM');
    this.dateBeforeTwo = moment(this.initDate).subtract(2, 'days').format('DD/MM');
    this.dateBeforeThree = moment(this.initDate).subtract(3, 'days').format('DD/MM');
    this.dateBeforeFour = moment(this.initDate).subtract(4, 'days').format('DD/MM');
    
  }

  ngOnInit() {
    
  }
  async receiver(e){

  }

  async HistoricalNewDate() {
    const modal = await this.modalCtrl.create({
      component: HistoricalNewDateComponent,
      cssClass:'tips_modal'
    });

    modal.onDidDismiss().then((dataReturned) => {
      if (dataReturned !== null) {
        
        this.initDate = dataReturned.data; 
        this.ionViewWillEnter()
        
      }
    });
    modal.present();
  }

}
